.profile-page {
  width: 100%;
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .user-header {
    width: 100%;
    display: flex;
    align-items: center;

    h2 {
      margin-bottom: 0;
      margin-left: 20px;
      font-weight: 800;
      font-size: 35px;
    }
  }

  .user-posts {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}